import React from "react";
import { Container, Badge, Form } from "react-bootstrap";
import styled from "styled-components";
import BTable from "react-bootstrap/Table";
import {
  useTable,
  useFilters,
  useGlobalFilter,
  useAsyncDebounce,
} from "react-table";
import { DocumentList, ProjectPhases } from "../components/DocumentList";
import TagColours from "../components/TagColours";

const TitleText = styled.div`
  text-align: center;
  font-size: 3rem;
  color: #50a712;
  font-weight: bold;
`;

const NormalText = styled.div`
  font-size: 1.5rem;
`;

const Tags = ({ values }) => {
  // Loop through the array and create a badge-like component instead of a comma-separated string
  return (
    <>
      {values.map((tag, idx) => {
        const textColour = TagColours[tag] ? TagColours[tag].text : TagColours['default'].text;
        const badgeColour = TagColours[tag] ? TagColours[tag].bg : TagColours['default'].bg;
        return (
          <Badge key={idx} style={{ marginLeft: "3px" }} bg={"none"} text={textColour} style={{ backgroundColor: badgeColour, marginLeft: "3px" }} >
            {tag}
          </Badge>
        );
      })}
    </>
  );
};

const Phases = ({ values }) => {
  // Loop through the array and create a badge-like component instead of a comma-separated string
  return (
    <>
      {values.map((phase_id, idx) => {
        const phase = ProjectPhases.filter((p) => p.phase === phase_id)[0];
        const textColour = phase ? phase.text : 'dark';
        const badgeColour = phase ? phase.bg : 'white';
        const phaseName = phase ? phase.name : 'unknown';

        return (
          <Badge key={idx} style={{ marginLeft: "3px" }} bg={"none"} text={textColour} style={{ backgroundColor: badgeColour, marginLeft: "3px" }} >
            { phaseName }
          </Badge>
        );
      })}
    </>
  );
};

function GlobalFilter({
  preGlobalFilteredRows,
  globalFilter,
  setGlobalFilter,
}) {
  const count = preGlobalFilteredRows.length;
  const [value, setValue] = React.useState(globalFilter);
  const onChange = useAsyncDebounce((value) => {
    setGlobalFilter(value || undefined);
  }, 200);

  return (
    <span>
      Search:{" "}
      <input
        value={value || ""}
        onChange={(e) => {
          setValue(e.target.value);
          onChange(e.target.value);
        }}
        placeholder={`${count} records...`}
        style={{
          fontSize: "1.1rem",
          border: "0",
        }}
      />
    </span>
  );
}

function DefaultColumnFilter({
  column: { filterValue, preFilteredRows, setFilter },
}) {
  const count = preFilteredRows.length;

  return (
    <Form.Control
      value={filterValue || ""}
      onChange={(e) => {
        setFilter(e.target.value || undefined); // Set undefined to remove the filter entirely
      }}
      placeholder={`Search ${count} records...`}
    />
  );
}
function SelectColumnFilterArray({
  column: { filterValue, setFilter, preFilteredRows, id },
}) {
  // Calculate the options for filtering
  // using the preFilteredRows
  const options = React.useMemo(() => {
    const options = new Set();
    preFilteredRows.forEach((row) => {
      row.values[id].forEach((r) => {
        options.add(r);
      })
      
    });
    return [...options.values()];
  }, [id, preFilteredRows]);

  // Render a multi-select box
  return (
    <Form.Select
      value={filterValue}
      onChange={(e) => {
        console.log(e.target.value)
        setFilter(e.target.value || undefined);
      }}
    >
      <option value="">All</option>
      {options.map((option, i) => (
        <option key={i} value={option}>
          {option}
        </option>
      ))}
    </Form.Select>
  );
}
function Table({ columns, data }) {
  const filterTypes = React.useMemo(
    () => ({
      text: (rows, id, filterValue) => {
        return rows.filter((row) => {
          const rowValue = row.values[id];
          return rowValue !== undefined
            ? String(rowValue)
                .toLowerCase()
                .startsWith(String(filterValue).toLowerCase())
            : true;
        });
      },
    }),
    []
  );
  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Filter: DefaultColumnFilter,
    }),
    []
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    state,
    visibleColumns,
    preGlobalFilteredRows,
    setGlobalFilter,
  } = useTable(
    {
      columns,
      data,
      defaultColumn, // Be sure to pass the defaultColumn option
      filterTypes,
    },
    useFilters, // useFilters!
    useGlobalFilter // useGlobalFilter!
  );

  return (
    <BTable striped bordered hover size="lg" {...getTableProps()}>
      <thead style={{ verticalAlign: "top"}}>
        {headerGroups.map((headerGroup) => (
          <tr {...headerGroup.getHeaderGroupProps()}>
            {headerGroup.headers.map((column) => (
              <th {...column.getHeaderProps()}>
                {column.render("Header")}
                <div>{column.canFilter ? column.render("Filter") : null}</div>
              </th>
            ))}
          </tr>
        ))}
      </thead>
      <tbody>
        {rows.map((row, i) => {
          prepareRow(row);
          return (
            <tr {...row.getRowProps()}>
              {row.cells.map((cell) => {
                return <td {...cell.getCellProps()}>{cell.render("Cell")}</td>;
              })}
            </tr>
          );
        })}
      </tbody>
    </BTable>
  );
}

const Documents = () => {
  const data = React.useMemo(() => DocumentList, []);
  const columns = React.useMemo(
    () => [
      {
        Header: "Document Name",
        accessor: "doc_name",
        Cell: ({ cell: { value }}) => <span style={{ fontWeight: 'bold'}}>{value}</span>
      },
      { Header: "Document Custodian",
        accessor: "custodion_org"
      },
      { Header: "Project Document Type",
        accessor: "doc_type",
        /*Filter: SelectColumnFilter,
        filter: "includes",    */  
      },
      {
        Header: "Applicable Phases",
        accessor: "phases",
        Cell: ({ cell: { value } }) => <Phases values={value} />,
        Filter: SelectColumnFilterArray,
        filter: "some"
      },

    ],
    []
  );

  return (
    <Container style={{ marginTop: "65px", minHeight: "100vh" }}>
      <TitleText>Documents</TitleText>
      <hr />
      <div>
        <Table columns={columns} data={data} />
      </div>
    </Container>
  );
};

export default Documents;
