import React, { useEffect, useState } from "react";
import Header from "./components/Header";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import 'mapbox-gl/dist/mapbox-gl.css';
import background from "./images/background.png";
import { parse as parseWkt } from "wellknown";

import Home from "./pages/Home";
import DeliveryTeam from "./pages/DeliveryTeam";
import Documents from "./pages/Documents";
import Framework from "./pages/Framework";
import Reporting from "./pages/Reporting";
import Map from "./pages/Map";

function App() {
  const [token, setToken] = useState(null);
  const [features, setFeatures] = useState(null);
  const [schema, setSchema] = useState([]);
  const [data, setData] = useState([]);

  const [viewport, setViewport] = useState({
    latitude: -45.05,
    longitude: 168.65,
    zoom: 14,
    bearing: 0,
    pitch: 65,
  });

  useEffect(async () => {
    const table = "ud_safety_improvements";
    const loadtype = 3;

    const tokenOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        referer: "https://www.stantec.com",
      },
      body: JSON.stringify({
        database: "Queenstown-Lakes District Council",
        userName: "bankiain",
        password: "aug2018#",
      }),
    };

    const tokenFetch = await fetch(
      "https://api.ramm.co.nz/v1/authenticate/login",
      tokenOptions
    );
    const token = await tokenFetch.json();

    const siteOptions = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        tableName: table,
        expandLookups: true,
        getGeometry: true,
        isLongitudeLatitude: true,
        loadType: loadtype,
      }),
    };

    const sitesFetch = await fetch(
      `https://api.ramm.co.nz/v1/data/table?token=${token}`,
      siteOptions
    );
    const data = await sitesFetch.json();

    const schemaOptions = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    };

    const schemaFetch = await fetch(
      `https://api.ramm.co.nz/v1/schema/${table}?loadType=${loadtype}&token=${token}`,
      schemaOptions
    );
    const schema = await schemaFetch.json();

    let columnPositions = schema.map((c) => c.columnName);
    columnPositions.push("wkt_geom");
    /*
    const lines = data.rows.filter((p) => {
      return p.values[p.values.length-1].includes("LINE")}
      );
    
    console.log(lines)
    */

    let dataOut = [];

    let features = [];
    
    data.rows.forEach((dataRow) => {
      let props = {};
      let geom = '';

      dataRow.values.forEach((f, i) => {
        let colName = columnPositions[i];
        if (colName != "wkt_geom") props[colName] = f;
        else geom = f;
      });
      if (props.activity_class == "Road to Zero") {
        dataOut.push(props);
        features.push({
          type: "Feature",
          properties: props,
          geometry: parseWkt(geom),
        });
      }
    });

    console.log(features)
    const featureCollection = {
      type: "FeatureCollection",
      features: features,
    };
    console.log(dataOut);
    setData(dataOut);
    setSchema(schema);
    setToken(token);
    setFeatures(featureCollection);
  }, []);

  return (
    <div
      style={{
        backgroundImage: `linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.7)), url(${background})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        backgroundRepeat: "no-repeat",
        minHeight: "100%",
      }}
    >
      <Router>
        <Header />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="delivery-team" element={<DeliveryTeam />} />
          <Route path="framework" element={<Framework />} />
          <Route path="documents" element={<Documents />} />
          <Route path="reporting" element={<Reporting />} />
          <Route
            path="map"
            element={
              <Map
                data={features}
                viewport={viewport}
                setViewport={setViewport}
                key="1"
              />
            }
          />
          <Route path="home" element={<Home />} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;
