import { People, Organisations, Roles } from "../components/TeamInfo";
import { Card, Col } from "react-bootstrap";
import styled from "styled-components";

const PersonName = styled.p`
  margin-top: 0px;
  margin-bottom: 2px;
  font-weight: 600;
`;

const CardText = styled.p`
  margin-top: 0px;
  margin-bottom: 2px;
  font-weight: 300;
`;

const ContactCard = (props) => {
  const persons = People.filter((p) => p.person_id === props.person_id);
  const role = Roles.filter((r) => r.role_id === props.role_id);

  if (persons.length === 0) {
    console.log("Missing data for PersonID: ", props.person_id);
    return <></>;
  }
  const person = persons[0];
  const role_name = role.length === 0 ? "Unknown" : role[0].role_name;
  const person_name = person.first_name + " " + person.last_name;
  const photo_path =
    person.photo === ""
      ? "/images/people/default.png"
      : "/images/people/" + person.photo;
  const org_photo = "/images/organisations/" + Organisations[person.organisation];

  return (
    <Col>
      <Card style={{ maxHeight: "150px", fontFamily: "MyFont" }}>
        <Card.Header
          style={{
            color: "white",
            fontWeight: "400",
            backgroundColor: "#00355f",
          }}
        >
          {role_name}
        </Card.Header>
        <div className="row no-gutters">
          <div className="col-auto">
            <img
              src={photo_path}
              className="img-fluid"
              alt=""
              style={{ maxHeight: "100px", padding: "5px" }}
            />
          </div>
          <div className="col">
            <div
              style={{
                paddingTop: "5px",
                marginLeft: "0px",
                backgroundImage: `linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.7)), url(${org_photo})`,
                backgroundSize: "auto 90px",
                backgroundPosition: "center center",
                backgroundRepeat: "no-repeat",
                minHeight: "100%"
              }}
            >
              <PersonName> {person_name}</PersonName>
              <CardText> {person.organisation} </CardText>
              <CardText> {person.email} </CardText>
            </div>
          </div>
        </div>
      </Card>
    </Col>
  );
};

export default ContactCard;
