import React from 'react';
import { Container } from 'react-bootstrap';
import styled from 'styled-components';

const TitleText = styled.div`
  text-align: center;
  font-size: 3rem;
  color: #50a712;
  font-weight: bold;
`;

const NormalText = styled.div`
  font-size: 1.5rem;
`;
const Reporting = () => {
  return (
    <Container  style={{ marginTop: "65px", minHeight: "100vh"}}>
      <TitleText>Reporting</TitleText>
      <hr />

    </Container>

  );
};

export default Reporting;
