import React from 'react';
import { Container } from 'react-bootstrap';
import styled from 'styled-components';
import rtz from '../images/road-to-zero.png';

const TitleText = styled.div`
  text-align: center;
  font-size: 3rem;
  color: #50A712;
  font-weight: bold;
`;

const NormalText = styled.div`
  font-size: 1.5rem;

`;

const Home = () => {
  return (
    <Container style={{ marginTop: "66px", minHeight: "100vh"}}>
      <TitleText>On the "Road to Zero"</TitleText>
      <hr/>
      <NormalText>
        In line with the government's <a href="https://www.nzta.govt.nz/safety/what-waka-kotahi-is-doing/nz-road-safety-strategy/">Road to Zero</a> safety targets, the Queenstown Lakes District Council (QLDC) is spending <strong>$25M throughout 2021 to 2024 </strong>to implement the <i>Minor Improvement Programme</i>.
        <br/>This is a much larger programme of work than in previous years and will require structure, visibility and accountability to ensure the required safety and value for money  outcomes are achieved. 
      </NormalText>
        <TitleText><img src={rtz} height={150} alt="road to zero logo" /></TitleText>
      <br/>
      <TitleText>The Delivery Framework</TitleText>
      <hr/>
      <NormalText>
      The Delivery Framework is a legacy tool for internal use by QLDC’s infrastructure team and Engineering and Specialist Support Services (ESSS) panel providers to deliver these critical  programme outcomes.
      <br/><br/>
      <strong>Specifically, the Delivery Framework will:</strong>
      <ul>
        <li>Provide a visual and auditable process on  how QLDC is delivering its annual Minor Improvement Programme to  QLDC’s funders, executive management team, Councillors and the Community</li>
        <li>Empower the collective QLDC/Stantec infrastructure team in terms of local  ownership and delivery of the Minor Improvement Programme, fostering a  collaborative working environment</li>
        <li>Be a  web based, QLDC owned, shared site that documents and links the key tools that  provide end to end delivery of QLDC’s  Minor Improvement Programme</li>
      </ul>
      </NormalText>
    </Container>

  );
};

export default Home;
