import React, { useCallback, useState } from "react";
import { Container } from "react-bootstrap";
import styled from 'styled-components';

import ReactMapGL, { Source, Layer, Popup } from "react-map-gl";

const ToolTipMB = styled.div`
position: absolute;
margin: 8px;
padding: 4px;
background: rgba(0, 0, 0, 0.8);
color: #fff;
max-width: 300px;
font-size: 10px;
z-index: 999;
pointer-events: none;
`;
const MAPBOX_TOKEN =
  "pk.eyJ1IjoicGV0ZXJoaWxsLXN0YW50ZWMiLCJhIjoiY2s5dng1MHNxMDN1cTNlcHM0dWxyMWlwbyJ9.-TrrrB41bgreAb5u7-SrSw";

const skyLayer = {
  id: "sky",
  type: "sky",
  paint: {
    "sky-type": "atmosphere",
    "sky-atmosphere-sun": [0.0, 0.0],
    "sky-atmosphere-sun-intensity": 15,
  },
};

const layerStyle = {
  id: "point",
  type: "line",
  layout: {
    "line-join": "round",
    "line-cap": "round",
  },
  paint: {
    "line-opacity": 0.8,
    "line-color": "yellow",
    "line-width": 5,
  },
};

const Map = (props) => {

  const [hoverInfo, setHoverInfo] = useState(null);

  const viewport = props.viewport;
  const setViewport = props.setViewport;

  const onMapLoad = useCallback((evt) => {
    const map = evt.target;
    map.setTerrain({ source: "mapbox-dem", exaggeration: 1.5 });
  }, []);

  const onHover = useCallback(event => {
    const {
      features,
      srcEvent: {offsetX, offsetY}
    } = event;
    const hoveredFeature = features && features[0];
    
    setHoverInfo(
      hoveredFeature
        ? {
            feature: hoveredFeature,
            x: offsetX,
            y: offsetY
          }
        : null
    );
  }, []);

  return (
    <Container fluid style={{ minHeight: "100vh", padding: "0px" }}>
      <div style={{ height: "100% !important" }}>
        <ReactMapGL
          {...viewport}
          width="100vw"
          height="100vh"
          mapStyle="mapbox://styles/mapbox/satellite-streets-v11"
          onViewportChange={(viewport) => setViewport(viewport)}
          mapboxApiAccessToken={MAPBOX_TOKEN}
          onLoad={onMapLoad}
          onHover={onHover}
          interactiveLayerIds={['sites']}
        >
          <Source
            id="mapbox-dem"
            type="raster-dem"
            url="mapbox://mapbox.mapbox-terrain-dem-v1"
            tileSize={512}
            maxzoom={14}
          />
          <Source id="site-data" type="geojson" data={props.data}>
            <Layer {...layerStyle} id='sites' />
          </Source>
          
          <Layer {...skyLayer} />
          {hoverInfo && (
          <ToolTipMB style={{left: hoverInfo.x, top: hoverInfo.y}}>
            <div>Project: {hoverInfo.feature.properties.projectname}</div>
            <div>Bundle: {hoverInfo.feature.properties.nzta_fund}</div>
            <div>Financial Year: {hoverInfo.feature.properties.financial_year}</div>
          </ToolTipMB>
        )}
        </ReactMapGL>
      </div>
    </Container>
  );
};

export default Map;
