import { Accordion, Row } from 'react-bootstrap';
import styled from "styled-components";
import { GroupNames, Groups } from "../components/TeamInfo"
import ContactCard from "../components/ContactCard";

const BannerText = styled.div`
  font-size: 1.9rem;
  color: #50a712;
  font-weight: 600;
`;

export const TeamCards = (props) => {
    const group_name = GroupNames.filter(g => g.group_id === props.group_id)[0].group_name;
    const groups = Groups.filter(g => g.group_id === props.group_id);

    return (
        <Accordion.Item
              key={props.group_id}
              eventKey={props.group_id}
              style={{ backgroundColor: "transparent" }}
            >
              <Accordion.Button
                style={{ backgroundColor: "transparent", borderRadius: "15px" }}
              >
                <BannerText>
                    { group_name }
                </BannerText>
              </Accordion.Button>
              <Accordion.Body>
              <Row xs={1} md={2} lg={3} className="g-4">
                  {
                      groups.map((item, index) => {
                          return (
                          <ContactCard person_id={item.person_id} role_id={item.role_id} key={item.person_id + '-' + item.role_id } />
                          )
                      })
                  }
                  </Row>
              </Accordion.Body>
            </Accordion.Item>
    )
};