import React from "react";
import { Container, Accordion } from "react-bootstrap";
import styled from "styled-components";
import { TeamCards } from "../components/TeamCards";


const TitleText = styled.div`
  text-align: center;
  font-size: 3rem;
  color: #50a712;
  font-weight: bold;
`;

const NormalText = styled.div`
  font-size: 1.5rem;
`;

const accordionItems = [12, 1, 3, 4, 5, 6, 7, 8, 9, 10, 11];

const DeliveryTeam = () => {
  return (
    <Container style={{ marginTop: "65px", minHeight: "100vh" }}>
      <TitleText id="team">Delivery Team</TitleText>
      <hr />
      <Accordion>
        {accordionItems &&
          accordionItems.map((item) => {
            return (
              <TeamCards group_id={item} key={item}/>
            )
          })}
      </Accordion>
    </Container>
  );
};

export default DeliveryTeam;
