import React from "react";
import { Container } from "react-bootstrap";
import styled from "styled-components";
import runway from "../images/runway.png";
import diagram from "../images/diagram.png";
import { RunwaySelection } from "../components/RunwaySelection";


const TitleText = styled.div`
  text-align: center;
  font-size: 3rem;
  color: #50a712;
  font-weight: bold;
`;

const BannerText = styled.div`
  text-align: center;
  font-size: 2.4rem;
  color: #50a712;
  font-weight: bold;
`;

const NormalText = styled.div`
  font-size: 1.5rem;
`;

const Framework = () => {
  return (
    <Container style={{ marginTop: "65px", height: "100%" }}>
      <TitleText>Programme Delivery Framework Overview</TitleText>
      <hr />
      <NormalText>
        The programme delivery framework documents the critical, end to end,
        decision steps to deliver projects within QLDC’s annual minor
        improvement programme. <br />
        <br />
        The overall process is descibed in the steps detailed below.
      </NormalText>
      <br />
      <BannerText>Step 1 - Project Confirmation</BannerText>
      <hr />
      <NormalText>
        The confirmed programme is stored and maintained in RAMM in the user-defined table: <strong><i>ud_safety_improvements</i></strong>. <br />
        Each project in RAMM may consist of a number of smaller 'bundled'
        project sites. <br />
        Access to the programme and it's details can be found in the Reporting
        section of this website. <br />
      </NormalText>
      <hr />
      <BannerText>Step 2 - Project Runway Selection</BannerText>
      <hr />
      <NormalText>
        Each project in the confirmed programme has cost, design complexity, and
        risk ratings. This information is stored against each project record in
        RAMM. <br />
        The combination of these ratings determine the project's delivery{" "}
        <strong>Runway</strong> and <strong>Runway Entry Point</strong>.
        <br /> <br />
        The project's <strong>Runway</strong> determines the inputs, checks, and
        outputs required to successfully deliver that project.
        <br />
        The decision logic to calculate the <strong>Runway</strong> is
        demonstrated interactively <a href="#runway">here</a>
        <br />
        <br />
      </NormalText>
      <hr />
      <BannerText>Step 3 - Project Runway Delivery</BannerText>
      <NormalText>
        The diagram below details the entry points, delivery phases, and gateway
        approvals for each <strong>Project Runway</strong>
        <br /> The project phases for each <strong>Runway</strong> are
        demonstrated interactively <a href="#">here</a>
        <br />
      </NormalText>
      <br />
      <img src={diagram} alt="" style={{ width: "100%" }} />

      <br />
      <hr />
      <BannerText>Project Runway Tool</BannerText>
      <div id="runway" style={{ paddingBottom: "50px" }}>
        <RunwaySelection />
      </div>
    </Container>
  );
};

export default Framework;
